.post {
  position: relative;
  width: 280px;
  padding-top: 15px;
  padding-bottom: 27px;
}

@media (max-width: $mobile-only) {
  .post {
    &::before {
      content: "";
      position: absolute;
      width: 320px;
      height: 1px;
      background-color: #e5e5e5;
      top: -8px;
      left: 50%;
      transform: translate(-50%);
    }

    &:last-child::after {
      content: "";
      position: absolute;
      width: 320px;
      height: 1px;
      background-color: #e5e5e5;
      bottom: 7px;
      left: 50%;
      transform: translate(-50%);
    }
  }
}

.post__heading {
  @include resetIndentation;
  padding-top: 16px;
  padding-right: 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  color: #000000;
  text-transform: uppercase;
}

.post__author {
  @include resetIndentation;
  line-height: 24px;
  color: #333333;
}

.post__like {
  position: relative;
  padding-top: 10px;
  padding-right: 20px;
  padding-left: 37px;
}

.post__like::before {
  content: "";
  position: absolute;
  background-image: url("../img/icon-like.svg");
  background-repeat: no-repeat;
  top: 6px;
  left: 0;
  width: 23px;
  height: 21px;
  opacity: 0.3;
}

.post__photo {
  display: block;
}

@media (min-width: $tablet-width) {
  .post {
    width: 324px;
    padding-top: 0;
    padding-bottom: 16px;
    background-color: #f2f2f2;

    &--lead {
      width: 100%;
    }

    &--special {
      background-color: #eaeaea;
    }
  }

  .post__photo {
    width: 100%;
    height: auto;
  }

  .post__data {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .post__description {
    padding-left: 20px;
  }

  .post__like {
    margin-top: 6px;
    min-width: 32px;

    &::before {
      top: 7px;
      left: 5px;
    }
  }
}

@media (min-width: $desktop-width) {
  .post {
    position: relative;
    width: 400px;
    padding-bottom: 0;

    &--lead {
      width: 100%;
    }
  }

  .post__heading {
    color: #ffffff;
  }

  .post__author {
    color: #ffffff;
  }

  .post__likes {
    color: #ffffff;
  }

  .post__like {
    &::before {
      display: none;
    }

    &::after {
      content: "";
      position: absolute;
      background-image: url("../img/icon-like-white.svg");
      background-repeat: no-repeat;
      top: 8px;
      left: 4px;
      width: 23px;
      height: 21px;
    }
  }

  .post__data {
    position: absolute;
    width: 100%;
    padding-bottom: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
  }
}
