.comment {
  width: 100%;
  height: 160px;
  margin: 0;
  padding-top: 17px;
  padding-right: 21px;
  padding-bottom: 22px;
  padding-left: 21px;
  box-sizing: border-box;
  font-family: $main-font;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  resize: none;
  background-color: #f2f2f2;
  border: none;
}

.comment::placeholder {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}

@media (min-width: $desktop-width) {
  .comment {
    width: 960px;
  }
}
